header {
 height:100vh;
 padding-top:1rem ;
 overflow:hidden;
 
}

.header__container{
    text-align:center;
    height:100%;
    position:relative;

}
/* =================CTA button==================== */
.cta{
    margin-top:3.5rem;
    display:flex;
    gap:1.25rem;
    justify-content: center;
}
/* =================Socials button==================== */
.header__socials{
    display:flex;
    flex-direction:column;
    align-items:center;
    gap:0.8rem;
    position:absolute;
    left:0;
    bottom:3rem;
    
}

.header__socials::after{
    content:"";
    width: 1px;
    height:2rem;
    background: var(--color-primary
    );
}
/* ================ME=================== */
.me {
   
    background-image:linear-gradient(var(--color-primary),transparent);
    width:22rem;
    height:30rem;
    position:absolute;
    left: calc(50% - 11rem);
    margin-top:4rem;
    border-radius:12rem 12rem 0 0;
    overflow:hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem ;
}
/* ===============SCROLL DOWN======================= */
.scroll__down {
    position:absolute;
    right:5.5rem;
    bottom :5rem;
    transform:rotate(90deg);
    font-size:0.9rem;
    font-weight: 300;
}

/* ===============================MEDIA QUERIES MEDIUM SIZE DEVICES================================== */
@media screen and (max-width:1024px){
 /* header{
  height : 68vh;  
 } */
}

/* ===============================MEDIA QUERIES SMALL SIZE DEVICES================================== */
@media screen and (max-width:600px){
 /* header{
  height : 68vh;  
  }  */
  
  .header__socials,
  .scroll__down{
    display:none;
  }
}