
@import url('https://fonts.googleapis.com/css2?family=Khand:wght@300;400;500;600&display=swap');
* {
    margin:0;
    padding:0;
    border:0;
    outline:0;
    text-decoration:none;
    box-sizing: border-box;
    list-style: none;
}
:root{
    --color-bg:hsl(240, 29%, 17%);
    --color-bg-variant:hsl(240, 42%, 30%);
    --color-primary:hsl(205, 100%, 65%);
    --color-primary-variant:hsla(205, 100%, 65%, 0.4);
    --color-white:hsl(0, 0%, 100%);
    --color-light:hsla(0, 0%, 100%, 0.6);

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;

    --transition: all 400ms ease;

}
html{
    scroll-behavior:smooth;

}
::webkit-scrollbar{
    display:none;

}
body{
    font-family: 'Khand', sans-serif;
    background:var(--color-bg);
    color:var(--color-white);
    background-image:url(../src/assets/bg-texture.png) ;
    line-height:1.7;
}
/* ==================================GENERAL STYLING=================================================== */
.container{
    width:var(--container-width-lg);
    margin:0 auto;

}

h1,
h2,
h3,
h4,
h5{
    font-weight:500;
}

h1{
    font-size:2.5rem;
}

section{
    margin-top:8rem;
    

}
section > h2,
section > h5{
    text-align:center;
    color:var(--color-light);
}
section > h2{
    color:var(color-primary);
    margin-bottom:3rem;
}
.text-light{
    color:var(--color-light);
}

a {
    color:var(-color-primary);
    transition:var(--transition);
}
a:hover{
    color:var(--color-white);
}

.btn{
    width:max-content;
    display:inline-block;
    color:var(--color-primary);
    padding:0.75rem 1.25rem;
    border-radius:0.4rem;
    cursor:pointer;
    border:1px solid var(--color-primary);
    transition:var(--transition);

}
.btn:hover{
    background:var(--color-white);
    color:var(--color-white);
    border-color:transparent;
    cursor:zoom-in;

}
.btn-primary{
    background: var(--color-primary);
    color:var(--color-bg);
}

img{
    display:block;
    width:100%;
    object-fit:cover;
}

/* ===============================MEDIA QUERIES MEDIUM SIZE DEVICES================================== */
@media screen and (max-width:1024px){
.container{
    max-width: var(--container-width-md);
}
section{
    margin-top:6rem;

}
}
/* ------------------------SMALL SIZE DEVICES---------------------------- */
@media screen and (max-width:600px){

    .container{
        max-width: var(--container-width-sm);
    }
    section > h2 {
        margin-bottom:2rem;
    
    }

}
